// ./libs/swiper-bundle.min.js


'use strict';
$(document).ready(function() {
    $('.slider-for').slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        fade: true,
        asNavFor: '.slider-nav',
        lazyLoad: 'ondemand',
        infinite: false,
    });
    $('.slider-nav').slick({
        slidesToShow: 2,
        slidesToScroll: 1,
        asNavFor: '.slider-for',
        dots: true,
        focusOnSelect: true,
        prevArrow: $('.js-next-slide'),
        nextArrow: $('.js-next-prev'),
        appendDots: $('.slide-pagination'),
        infinite: false,
        responsive: [
            {
                breakpoint: 768,
                settings: {
                    variableWidth: true,
                    slidesToShow: 1,
                    slidesToScroll: 1,
                }
            }
        ]
    });

    var canvas = document.getElementById("canvasTop");
    var wrapper = document.getElementById('canvasImg');
    var wrapperStyle = window.getComputedStyle(wrapper, null);
    canvas.width = parseInt(wrapperStyle.getPropertyValue("width"));
    canvas.height = parseInt(wrapperStyle.getPropertyValue("height"));
    var ctx = canvas.getContext("2d");

    ctx.lineWidth = 62;
    ctx.lineCap = "round";
    ctx.lineJoin = "round";
    ctx.fillStyle = "rgba(255, 255, 255, 0.9)";
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    ctx.globalCompositeOperation = "destination-out";

    var canvasOffset = $("#canvasTop").offset();
    var offsetX = canvasOffset.left;
    var offsetY = canvasOffset.top;

    var startX;
    var startY;


    function handleMouseMove(e) {
        mouseX = parseInt(e.clientX - offsetX);
        mouseY = parseInt(e.clientY - offsetY);
        ctx.beginPath();
        ctx.moveTo(startX, startY);
        ctx.lineTo(mouseX, mouseY);
        ctx.stroke();
        startX = mouseX;
        startY = mouseY;
    }

    var countAllow = true;
    $("#home").mousemove(function (e) {
        handleMouseMove(e);
        if (countAllow) {
            countAllow = false;
            const data = ctx.getImageData(0, 0, canvas.width, canvas.height).data;
            const nrOfPixels = data.length / 4;
            let transparent = 0;
            for (let i = 3; i < data.length; i += 4) {
                transparent += data[i] ? 0 : 1;
            }
            const percentage = transparent / nrOfPixels * 100;
            if (percentage > 30) {
                $('.home__video-btn').fadeIn().css('pointer-events', 'auto');
            } else {
                setTimeout(function () {
                    countAllow = true;
                }, 1000);
            }
        }

    });






    function calcDeg() {
        let shapeH = $('.geo__bg').innerHeight();
        let shapeW = $('.geo__bg').innerWidth();
        let a = shapeH;
        let b = shapeW * 0.25;
        let deg = 90 - (180 / Math.PI * Math.atan2(a, b));
        $('.geo__list').css('transform', 'rotate(' + deg + 'deg)');
    }

    calcDeg();
    $(window).on('resize', function () {
        calcDeg();
    })


    let sections = document.querySelectorAll(".slideshow__slide");
    let scrollContainer = document.querySelector(".slideshow");
    let slideDuration = sections.length / (sections.length - 1);
    let tl1 = gsap.timeline();

    gsap.set('.slideshow', {width: sections.length + "00vw"});

    tl1.to(sections, sections.length, {xPercent: -100 * (sections.length - 1), ease: "none"}, 0);

    tl1.fromTo('.home__overlay', slideDuration, {left: '125%'}, {left: 0, ease: "none"}, 0);
    tl1.to('.home__video-btn', {className: 'home__video-btn no-event'}, slideDuration * 0.5);


    tl1.fromTo('.shape[data-id="0"]', slideDuration, {left: 0}, {left: "-125%", ease: "none"}, 0);
    $('.shape').each(function (index, shape) {
        if (index !== 0 && index !== (sections.length - 1)) {
            tl1.fromTo(shape, slideDuration * 2, {left: '125%'}, {
                left: "-125%",
                ease: "none"
            }, (index - 1) * slideDuration);
        } else if (index === (sections.length - 1)) {
            tl1.fromTo(shape, slideDuration, {left: '125%'}, {left: "0%", ease: "none"}, (index - 1) * slideDuration);
        }
    });


    gsap.set('.shape[data-id="0"] .shape__arrow', {left: "54%"});
    $('.shape__arrow').each(function (index, shape) {
        if (index !== 0) {
            tl1.fromTo(shape, slideDuration * 1, {left: '-100%'}, {
                left: "54%",
                ease: Power3.easeIn
            }, (index - 1) * slideDuration);
        }
    });

    $('.shape__number').each(function (index, shape) {
        if (index !== 0) {
            tl1.fromTo(shape, slideDuration * 1, {left: '100%%'}, {
                left: "50%",
                ease: Power3.easeIn
            }, (index - 1) * slideDuration);
        }
    });


    tl1.fromTo('.text[data-id="0"] .text__title', slideDuration, {left: 0}, {left: "-125%", ease: "none"}, 0);
    $('.text__title').each(function (index, shape) {
        if (index !== 0 && index !== (sections.length - 1)) {
            tl1.fromTo(shape, slideDuration, {left: '125%'}, {
                left: "0",
                ease: Power1.easeIn
            }, (index - 1) * slideDuration);
            tl1.fromTo(shape, slideDuration, {left: '0'}, {left: "-125%", ease: "none"}, (index - 0) * slideDuration);
        } else if (index === (sections.length - 1)) {
            tl1.fromTo(shape, slideDuration, {left: '125%'}, {
                left: "0%",
                ease: Power1.easeIn
            }, (index - 1) * slideDuration);
        }
    });


    let headerClass= $('.shape[data-id="0"]').data('header');
    if(headerClass && headerClass !== ''){
        $('.header').addClass(headerClass);
    }

    $('.shape').each(function (index, geo) {
        if (index !== 0) {
            let headerClass= $(geo).data('header');
            let setHeaderClass = 'header';
            if(headerClass && headerClass !== ''){
                setHeaderClass += ' '+headerClass;
            }
            tl1.to('.header', 0, {className: setHeaderClass}, (index - 0.5) * slideDuration);
        }


        if ($(geo).find('.geo__list').length > 0) {
            let shape = $(geo).find('.geo__list');
            tl1.fromTo(shape, slideDuration, {bottom: '-50%',left: '-80%'}, {
                bottom: "10%",
                left: "17%",
                ease: Power3.easeIn
            }, (index - 1) * slideDuration);
        }

        if ($(geo).find('.contacts__content').length > 0) {
            let shape = $(geo).find('.contacts__content');
            tl1.fromTo(shape, slideDuration, {bottom: '-100%'}, {
                bottom: "0",
                ease: Power3.easeIn
            }, (index - 1) * slideDuration);

            let shape2 = $('.contacts');
            tl1.fromTo(shape2, slideDuration, {left: '100%'}, {
                left: "0",
                ease: "none"
            }, (index - 1) * slideDuration);
        }

        if ($(geo).find('.gallery__left').length > 0) {
            let shape = $(geo).find('.gallery__left');
            tl1.fromTo(shape, slideDuration, {right: '-100%'}, {
                right: "7.4%",
                ease: Power3.easeIn
            }, (index - 1) * slideDuration);

            let shape2 = $('.gallery');
            tl1.fromTo(shape2, slideDuration, {left: '125%'}, {
                left: "0",
                ease: "none"
            }, (index - 1) * slideDuration);
        }
    });

    $('.text').each(function (index, about) {
        if ($(about).find('.about__text').length > 0) {
            let shape = $(about).find('.about__text');
            tl1.fromTo(shape, slideDuration, {top: '125%'}, {
                top: "0",
                ease: Power3.easeIn
            }, (index - 1) * slideDuration);
            tl1.fromTo(shape, slideDuration, {top: '0'}, {
                top: "-125%",
                ease: Power3.easeOut
            }, (index - 0) * slideDuration);
        }
        if ($(about).find('.about__year').length > 0) {
            let shape = $(about).find('.about__year');
            tl1.fromTo(shape, slideDuration, {top: '125%'}, {
                top: "0",
                ease: Power3.easeIn
            }, (index - 1) * slideDuration);
            tl1.fromTo(shape, slideDuration, {top: '0'}, {
                top: "-125%",
                ease: Power3.easeOut
            }, (index - 0) * slideDuration);
        }
        if ($(about).find('.about__year-text').length > 0) {
            let shape = $(about).find('.about__year-text');
            tl1.fromTo(shape, slideDuration, {top: '125%'}, {
                top: "0",
                ease: Power3.easeIn
            }, (index - 1) * slideDuration);
            tl1.fromTo(shape, slideDuration, {top: '0'}, {
                top: "-125%",
                ease: Power3.easeOut
            }, (index - 0) * slideDuration);
        }


        if ($(about).find('.geo__title').length > 0) {
            let shape = $('.geo__map-wrapper');
            tl1.fromTo(shape, slideDuration, {top: '125%'}, {
                top: "0",
                ease: Power3.easeIn
            }, (index - 1) * slideDuration);
            tl1.fromTo(shape, slideDuration, {left: '0'}, {left: "-125%", ease: "none"}, (index - 0) * slideDuration);
        }
    });


    let horizontalScroll = ScrollTrigger.create({
        animation: tl1,
        pinType: 'transform',
        trigger: scrollContainer,
        pin: true,
        scrub: 0.1,
        snap: {
            snapTo: 1 / (sections.length - 1),
            duration: {min: 0.1, max: 0.5},
            delay: 0,
            ease: "power1",
            inertia: false
        },
        end: () => "+=" + scrollContainer.offsetWidth*0.75,
    });

    var allowMouse = true;
    var minVelocity = 600;
    var minDistance = 50;

    var startTime, lastTime, vx, vy;

    var allowScroll = true;

    var dragRatio = scrollContainer.offsetWidth / (window.innerWidth * (sections.length - 1));
    var drag = Draggable.create(".proxy", {
        trigger: scrollContainer,
        type: "x",
        onPress() {
            if (allowScroll){
                this.startScroll = horizontalScroll.scroll();
            }
        },
        onDragStart: function() {
            if (allowScroll) {
                startTime = +Date.now() / 1000;
                lastTime = vx = vy = 0;
            }
        },
        onDrag() {
            if (allowScroll) {
                horizontalScroll.scroll(this.startScroll - (this.x - this.startX) * dragRatio);

                var time = +Date.now() / 1000;
                var deltaTime = time - lastTime;

                vx = this.deltaX / deltaTime;
                vy = this.deltaY / deltaTime;

                lastTime = time;
            }
        },
        onDragEnd: function() {
            if (allowScroll) {

                var dx = this.x - this.startX;
                var dy = this.y - this.startY;
                var x = Math.abs(dx);
                var y = Math.abs(dy);

                if (x > y && x > minDistance && Math.abs(vx) > minVelocity) {
                    if (dx > 0) {
                        //prev
                        let currentSlide = Math.floor($(window).scrollTop() / ((scrollContainer.offsetWidth / (sections.length - 1)) * 0.75));
                        if (currentSlide >= 0) {
                            let toScroll = scrollContainer.offsetWidth * 0.75 / (sections.length - 1) * (currentSlide);
                            gsap.to(window, {
                                duration: 0.8,
                                scrollTo: {y: toScroll, offsetY: 0}
                            });
                        }
                    } else {
                        //next
                        let currentSlide = Math.floor($(window).scrollTop() / ((scrollContainer.offsetWidth / (sections.length - 1)) * 0.75));
                        if (currentSlide < (sections.length - 1)) {
                            let toScroll = scrollContainer.offsetWidth * 0.75 / (sections.length - 1) * (currentSlide + 1);
                            gsap.to(window, {
                                duration: 1.1,
                                scrollTo: {y: toScroll, offsetY: 0}
                            });
                        }
                    }
                    allowScroll = false;
                    setTimeout(function (){
                        allowScroll = true;
                    },1110);
                } else if (y > x && y > minDistance && Math.abs(vy) > minVelocity) {
                    //box.text(dy > 0 ? "swipe down" : "swipe up");
                } else {
                    //box.text("");
                }
            }
        }
    })[0];


    $('.js-scroll').on('click',function (){
        event.preventDefault();
        let href = $(this).attr('href');
        if ($(href).length > 0){
            let toScroll = scrollContainer.offsetWidth*0.75/(sections.length-1)*($(href).index());
            if ($(href).index() === 0){
                toScroll = 0;
            }
            gsap.to(window, {
                duration: 1,
                scrollTo: { y: toScroll, offsetY: 0 }
            });
        }
    });

    /*let resizeAllow = true;
    $(window).on('resize',function (){
        if (resizeAllow && $(window).width < 768){
            resizeAllow = false;
            location.reload();
        }
    });*/

    $('.geo__list-item').on('click',function (){
        event.preventDefault();
        if (!$(this).hasClass('geo__list-item--big')){
            let data = $(this).data('id');
            $(this).addClass('geo__list-item--big').siblings().removeClass('geo__list-item--big');
            $('.geo__map path').removeClass('active');
            $('.geo__map path[data-id="'+data+'"]').addClass('active');
        }
    });
});